import {useState} from '@wordpress/element';
import {
    Button,
    ColorIndicator,
    Popover,
    ColorPicker,
    ColorPalette,
    Flex,
    FlexItem,
    BaseControl, useBaseControlProps
} from '@wordpress/components';
import colors from "../../colors";
import React from "react";

const AabColorPicker = ({value, onChange, label}) => {

    const [isVisible, setIsVisible] = useState(false);
    const [color, setColor] = useState(value);

    const toggleVisible = () => {
        setIsVisible(!isVisible);
    };
    const handleColorChange = (newColor) => {
        setColor(newColor);
        if (onChange) {
            onChange(newColor); // Pass the color to the parent
        }
    };
    return (
        <div className='aab-color-picker' style={{'position': 'relative'}}>
            <Flex>
                <FlexItem>
                    <BaseControl
                        label={label}
                    ></BaseControl>
                </FlexItem>
                <FlexItem>
                    <Flex>
                        <FlexItem>
                            <Button
                                className={`aab-reset-btn ${color ? 'active' : 'disabled'}`}
                                onClick={() => handleColorChange(undefined)}
                                icon='image-rotate'
                                showTooltip={true}
                                label='Reset'
                            >
                            </Button>
                        </FlexItem>
                        <FlexItem>
                            <Button onClick={toggleVisible}>
                                <ColorIndicator colorValue={color}/>
                            </Button>
                        </FlexItem>
                    </Flex>
                    {isVisible &&
                        <Popover className='aab-color-popover' onFocusOutside={() => setIsVisible(false)}>
                            <ColorPicker
                                enableAlpha
                                onChange={(newColor) => handleColorChange(newColor)}
                                color={color}
                            />
                            <div style={{'padding': '0 14px 16px 14px'}}>
                                <ColorPalette
                                    colors={colors}
                                    value={color}
                                    asButtons={true}
                                    disableCustomColors={true}
                                    onChange={(newColor) => handleColorChange(newColor)}
                                    clearable={false}
                                />
                            </div>
                        </Popover>}
                </FlexItem>
            </Flex>
        </div>
    );
};

export default AabColorPicker;